import React, { Component } from 'react';
import {H, Wrapper, Card, Header, UL, Inp, NewGame, Container} from "../style";
class Join extends Component {
    constructor(props){
        super(props);
        this.state = {
            newGame: false,
            roomname: ''
        }
    }

    componentDidMount() {
        this.props.socket.emit('getgames');
        this.props.socket.on('games',(g) =>{
            this.setState({
                games: g
            })
        })
    }
    render() {
        return (
            <Wrapper noalign>
                <H size={3} dark bold>Join room</H>
                <Card>
                    <Header size={1.5} dark bold>Games</Header>
                    <NewGame onClick={() => this.setState({newGame:!this.state.newGame})}>Create new game</NewGame>
                    {
                        this.state.newGame ? (
                            <Container>
                                <Inp placeholder={'Choose a roomname'} onChange={(e) => this.setState({roomname:e.target.value})}/>
                                <NewGame dark onClick={() => this.props.join(this.state.roomname)}>Join {this.state.roomname}</NewGame>
                            </Container>
                        ) : null
                    }
                    <UL>
                        {
                            Object.keys(this.props.games).map((game,i) => {
                                console.log(game)
                                return (<li key={`join-${i}`}onClick={() => this.props.join(game)}>{game}</li>)
                            })
                        }
                    </UL>
                </Card>
            </Wrapper>
        );
    }
}

export default Join;
