import React, { Component } from 'react';
import socketIOClient from "socket.io-client";
import Login from "./Login";
import Join from "./Join";
import Game from "./Game";
import {OuterWrapper} from "./style.js";
const socket = socketIOClient('localhost');

class App extends Component {
    constructor(props){
        super(props);
        this.state = {
            user: null,
            ingame: false,
            games: {}
        };
    }

    componentDidMount() {
        let usr = localStorage.getItem('username');
        if(typeof usr === "string"){
           this.login(usr)
        }else {
            this.setState({
                user: false
            });
        }
        socket.on('games', (g) =>{
            this.setState({
                games:g
            })
        });
        socket.on('granted', () =>{
            this.setState({
                ingame:true
            })
        })
        socket.on('failed', () =>{
            this.setState({
                ingame:false
            })
        })
    }
    login = u =>{
        socket.emit('login', u);
        localStorage.setItem('username', u);
        this.setState({
            user: u
        });
    };
    joingame = g => {
        socket.emit('joinroom',g)
    };
    render() {
    return (
        <OuterWrapper>
        {
            this.state.ingame ? (<Game socket={socket}/>) :
            this.state.user === false ? (<Login login={this.login}/>) : (<Join socket={socket} games={this.state.games} join={this.joingame}/>)
        }
        </OuterWrapper>
    );
  }
}

export default App;
