import React, { Component } from 'react';
import {Wrapper, Column, Row, GameInput, Word, Start, User, WhiteHeader, ArticleImg, NextWord, Title, Timer} from "../style";
class Game extends Component {
    constructor(props){
        super(props);
        this.state = {
            running: false,
            countdown: false,
            word:false,
            nextWord:"",
            room:{
                users:[],
            },
            ingameroom:{users:{}},
            articles: {},
            wordIndex:0,
            articleIndex:0,
            winner:null,
            timeleft:60,
        }
    }

    componentDidMount() {
        const {socket} = this.props;
        socket.emit('getroom');
        socket.on('starting', (s) =>{

            const c = setInterval(() =>{
                this.setState({countdown: this.state.countdown-1})
            },1000);
            this.setState({countdown: 3, running:true});
            setTimeout(() => {
                clearInterval(c)
                let timer = setInterval(()=>{
                    if(this.state.timeleft > 0){
                        this.setState({
                            timeleft:this.state.timeleft -=1,
                        })
                    }else{clearInterval(timer)}
                },1000)
            },3000)
        });
        socket.on('started', (r) => {
            this.setState({articles: r.article.articles})
            this.newWord()
        });
        socket.on('room', (r) =>{
            this.setState({room: r})
        });
        socket.on('done', (room) =>{
            let l = Object.keys(room.users).sort((a,b) => room.users[a] > room.users[b])
            this.setState({
                winner: l[0],
                running:false,
            })
            setTimeout(() =>{
                window.location.reload()
            },20000)
        });
        socket.on('update', (r) =>{
            if(r === this.state.ingameroom) return;
            const reducer = (accumulator, currentValue) => accumulator + currentValue;
            let aggval = Object.keys(this.state.ingameroom.users).map((u) =>this.state.ingameroom.users[u])
                .reduce(reducer,0);
            this.setState({
                ingameroom: r,
                aggval:aggval,
            })
        });
        if(this.state.running){
            this.input.focus()
        }
    }
    componentDidUpdate(){
        if(this.input){
            this.input.focus()
        }
    }
    newWord(){
        if(this.state.word  === false){
            this.setState({
                word:this.state.articles[this.state.articleIndex].description[this.state.wordIndex],
                nextWord: this.state.articles[this.state.articleIndex].description[this.state.wordIndex+1],
                wordIndex:1
            });
            return;
        }
        if(this.state.articles[this.state.articleIndex].description.length-2 === this.state.wordIndex){
            this.setState({
                articleIndex:this.state.articleIndex += 1,
                wordIndex: 0,
            })
        }
        this.setState({
            nextWord:this.state.articles[this.state.articleIndex].description[this.state.wordIndex+1],
            word:this.state.nextWord,
            wordIndex:this.state.wordIndex+1
        })
    }
    inpChange(e){
        let val = e.target.value;
        if(val.toLowerCase() === this.state.word.toLowerCase()){
            this.props.socket.emit('updatepoint',{room:this.state.ingameroom.title});
            e.target.value = "";
            this.newWord()
        }
    }
    startgame(){
        this.props.socket.emit('start')
    }
    render() {
        return (
            <Wrapper dir={'row'}>
                <Timer>{this.state.timeleft}:00</Timer>
                    <Column size={9}>
                        {
                            this.state.winner === null ? (
                                <>
                        <Row exception>
                            <NextWord>{this.state.nextWord ? this.state.nextWord : ""}</NextWord>
                            <Word>{this.state.word ? this.state.word : this.state.countdown ? this.state.countdown : 'Waiting...'}</Word>
                        </Row>
                        <Row>
                            <GameInput onChange={(e) => this.inpChange(e)} ref={(i) => {
                                this.input = i;
                            }}/>
                        </Row>
                   </> ) : (<Word>The winner is {this.state.winner}!</Word>) }
                    </Column>
                <Column size={3} color={'black'}>
                    {
                        this.state.running ? (
                            <>
                                <ArticleImg src={this.state.articles[this.state.articleIndex] ? this.state.articles[this.state.articleIndex].urlToImage : ""}/>
                                <Title>{this.state.articles.length ? this.state.articles[this.state.articleIndex].title : ""}</Title>
                                {
                                   Object.keys(this.state.ingameroom.users).map(u => (
                                            <User size={this.state.ingameroom.users[u]/(this.state.aggval/Object.keys(this.state.ingameroom.users).length)}>{u}:{this.state.ingameroom.users[u]}</User>
                                        )
                                    )
                                }
                            </>
                            ) : (
                            <>
                                <WhiteHeader>Users:</WhiteHeader>
                            {
                                this.state.room.users.map(u => (
                                    <User>{u}</User>
                                    )
                                )
                            }
                            <Start onClick={() => this.startgame()}>Start Game</Start>
                            </>)
                    }
                </Column>

            </Wrapper>
        );
    }
}

export default Game;
