import styled, {keyframes} from "styled-components";
const c = {
    red: '#E51C30',
    blue: '#00b9f2',
    black:'#141517',
    white:'#f6f6f6',

}
export const grow = keyframes`
    from{transform: scaleY(0)}
    to{transform: scaleY(1)}
`;
export const shake = keyframes`
    0% { -webkit-transform: translate(2px, 1px) rotate(0deg); }
	10% { -webkit-transform: translate(-1px, -2px) rotate(-1deg); }
	20% { -webkit-transform: translate(-3px, 0px) rotate(1deg); }
	30% { -webkit-transform: translate(0px, 2px) rotate(0deg); }
	40% { -webkit-transform: translate(1px, -1px) rotate(1deg); }
	50% { -webkit-transform: translate(-1px, 2px) rotate(-1deg); }
	60% { -webkit-transform: translate(-3px, 1px) rotate(0deg); }
	70% { -webkit-transform: translate(2px, 1px) rotate(-1deg); }
	80% { -webkit-transform: translate(-1px, -1px) rotate(1deg); }
	90% { -webkit-transform: translate(2px, 2px) rotate(0deg); }
	100% { -webkit-transform: translate(1px, -2px) rotate(-1deg); }
`;
export const fadeIn = keyframes`
    from{filter:opacity(0)}
    to{filter:opacity(1)}
`;
export const OuterWrapper = styled.div`
    height:100%;
    width:100%;
`;
export const Form = styled.form`
    border-radius: 5px;
    display:flex;
    flex-direction:column;
`;
export const Wrapper = styled.div`
    height:100%;
    width:100%;
    background: ${c.white};
    animation: ${fadeIn} ${props => props.time ? props.time : .3}s ease;
    display:flex;
    flex-direction:${props => props.dir ? props.dir : 'column'};
    justify-content:center;
    align-items: center;
`;
export const H = styled.h1`
    margin:.5em;
    display:block;
    position: absolute;
    color: ${props => props.dark ? c.black : c.blue};
    top:0;
    left:0;
    font-family: ${props => props.bold ?'LFT Etica Bold' :'NRK Etica Slab'};
    width:calc(100% - 1em);
    font-size:${props => props.size ? props.size : 1}em;
`;
export const Input = styled.input`
    border:0;
    border-radius:3px;
    background: ${c.blue};
    font-family: LFT Etica Bold;
    padding: 0.3em 1em;
    letter-spacing: 1.6;
    color:white;
    font-size:1em;
    height:3em;
    width:9em;
    ::-webkit-input-placeholder{
        color:white;
    }
`;
export const Header = styled(H)`
    top:initial;
    left:initial;
    position:initial;
`;
export const Card = styled.div`
    border:0;
    display:block;
    width:100%;
    max-width:1000px;
    max-height:600px;
    overflow-y:auto;
    background: white;
    letter-spacing: 1.6;
    color:${c.black};   
`;
export const UL = styled.ul`
     font-family: LFT Etica Slab;
     padding:0;
     box-sizing:border-box;
     & > *{
        box-sizing:border-box;
        list-style:none;
        width:95%;
        cursor:pointer;
        border:0;
        font-family: NRK Etica Slab;
        font-weight:600;
        color:${c.white};
        margin:2.5%
        text-align:left;
        padding:0.5em 1em;
        font-size:1em;
        background:${c.black}
     }
`;
export const NewGame = styled.button`
    width:95%;
    cursor:pointer;
    border:0;
    font-family: NRK Etica Slab;
    font-weight:600;
    color:${props => props.dark ? c.white:c.black};
    margin:2.5%
    padding:1em 2em;
    font-size:1.2em;
    background:${props => props.dark ? c.black : c.blue}
`;
export const Container = styled.div`
    transform-origin: top center;
    animation: ${grow} .3s ease;
    width:100%;
`;
export const Inp = styled.input`
    border:2px #ccc solid;
    background: ${c.white};
    font-family: LFT Etica Bold;
    padding: 1em 2em;
    letter-spacing: 1.6;
    color:${c.black};
    font-size:1em;
    width:95%
    margin:0 2.5%;
    box-sizing:border-box;
    ::-webkit-input-placeholder{
        color:#ccc;
    }
`;
export const Column = styled.div`
    display:flex;
    flex-direction:column;
    flex-grow: ${props => props.size ? props.size : 1};
    width: ${props => props.size ? props.size/16+'%' : 1};
    height:100%;
    align-items:center;
    background:${props => props.color ? c[props.color] : c.white};
`;
export const Row = styled.div`
    display:flex;
    flex-direction:${props => props.exception ? 'column':'row'};
    height:100%;
    background:${props => props.color ? c[props.color] : c.white};
    align-items:center;
`;
export const GameInput = styled.input`
    flex-grow:1;
    border:0;
    background:transparent;
    height:90px;
    color:${c.blue};
    text-align:center;
    border-radius:3px;
    font-size:80px;
    outline: auto;
    display:${props => props.display ? 'block' :'inline'}
    &:focus{
        outline:0
    }
`;
export const Word = styled.h1`
    color:${c.red};
    text-align:center;
    width:100%;
    font-size:80px;
`;
export const Start = styled.button`
    width:95%;
    cursor:pointer;
    border:0;
    font-family: NRK Etica Slab;
    font-weight:600;
    color:${c.white};
    margin:2.5%
    padding:1em 2em;
    font-size:1.2em;
    margin-top:auto;
    background:${props => props.dark ? c.black : c.blue}
`;
export const User = styled.h2`
    text-align:center;
    width:50%;
    margin:0;
    margin-top:5px;
    font-size:${props => props.size ? props.size*1.2 : 1.2})em;
    color:${c.white};
    animation: ${props => props.size > 1 ? shake : ""} 1s ease infinite;
    letter-spacing:1.6;
`;
export const WhiteHeader = styled.h2`
    font-family: NRK Etica Slab;
    font-weight:600;
    color:${c.white};
    letter-spacing:1.6;
    text-align:center;
`;
export const ArticleImg = styled.img`
    width:100%;
    margin:0;
    padding:0;
    transition:all .3s ease;
    animation:${fadeIn}.3s ease;
`;
export const NextWord = styled(Word)`
    opacity:0.6;
    margin:0;
    transform:scale(0.5);
`;
export const Title = styled.h1`
    color: ${c.blue};
    text-align: center;
     font-family: NRK Etica Slab;
`;
export const Timer = styled.h1`
    font-size:4em;
    color:${c.blue};
    opacity:0.4;
    position:fixed;
    top:0;
    left:0;
    margin:.2em;
    z-index:10;
`;
