import React, { Component } from 'react';
import {Wrapper, H, Input, Form} from "../style";

class Login extends Component {
    constructor(props){
        super(props)
        this.state = {
            username: ''
        }
    }

    componentDidMount() {
    }
    render() {
        return (
            <Wrapper>
                <H size={3} bold>Login</H>
                <Form onSubmit={(e) => {
                    e.preventDefault()
                    if(this.state.username == null) return;

                    this.props.login(this.state.username)
                }
                }>
                    <Input onChange={(e) => {
                        this.setState({
                            username: e.target.value
                        })
                    }} placeholder={'Username'} />
                </Form>
            </Wrapper>
        );
    }
}

export default Login;
